<template>
  <div>
    <template v-if="!opened_font_item_path">
      <div>
        <div v-for="font in fonts" :key="font.$path">
          <FontItem
            :context="'preview'"
            :font="font"
            @toggle="openFontItem(font.$path)"
          />
        </div>
      </div>

      <br />

      <button
        type="button"
        class="u-button u-button_bleuvert"
        :class="{
          'is--active': show_create_font,
        }"
        @click="show_create_font = !show_create_font"
      >
        {{ $t("add") }}
      </button>

      <form
        class="input-validation-required"
        v-if="show_create_font"
        @submit.prevent="createFont"
      >
        <fieldset>
          <legend class="u-label">{{ $t("add_font") }}</legend>

          <TextInput
            :content.sync="new_font_title"
            :label_str="'font_name'"
            :required="true"
            :input_type="'text'"
          />

          <br />
          <br />

          <button
            slot="footer"
            class="u-button u-button_bleuvert"
            :disabled="new_font_title.length === 0"
            type="submit"
          >
            {{ $t("create") }}
          </button>
        </fieldset>
      </form>
    </template>
    <template v-else>
      <FontItem
        :context="'full'"
        :font="opened_font_item"
        @toggle="opened_font_item_path = false"
      />
    </template>
    <!-- 
    LIST OF FONTS 

    CREATE 

    FONT NAME

    FONT FILES : 
    400 normal
    400 ital
    700 normal
    700 ital
-->
  </div>
</template>
<script>
import FontItem from "@/adc-core/ui/FontItem.vue";

export default {
  props: {},
  components: {
    FontItem,
  },
  data() {
    return {
      fonts: undefined,
      show_create_font: false,

      opened_font_item_path: false,

      new_font_title: "",

      path: "fonts",
    };
  },
  created() {},
  async mounted() {
    this.fonts = await this.$api
      .getFolders({
        path: this.path,
      })
      .catch((err) => {
        this.fetch_fonts_error = err.response;
        return;
      });
    this.$api.join({ room: this.path });
  },
  beforeDestroy() {},
  watch: {},
  computed: {
    opened_font_item() {
      return this.fonts.find((f) => f.$path === this.opened_font_item_path);
    },
  },
  methods: {
    async createFont() {
      const font_slug = await this.$api.createFolder({
        path: this.path,
        additional_meta: {
          title: this.new_font_title,
          requested_slug: this.new_font_title,
          $status: "public",
        },
      });
      this.openFontItem(this.path + "/" + font_slug);
    },
    openFontItem(path) {
      this.opened_font_item_path = path;
    },
  },
};
</script>
<style lang="scss" scoped></style>
